.header {
  min-width: 100%;
  flex: 0 0 70px;
  background-color: #519a83;
  display: flex;
  align-items: center;

}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 10px;
  font-size: 18px;
}

.item {

}

.link {
  &:hover{
    color: #41c20e;
  }
  color: inherit;
  &.active {
    text-decoration: underline;
  }

}

.buttonOut {
  color: wheat;
  padding: 5px;
  border-radius: 5px;
  background-color: #692224;
  font-size: 16px;
  letter-spacing: 1.5px;
  transition: all 0.2s ease 0s;

}