.container {
  height: 100%;
  background-color: #91d5bf;
  padding: 10px;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-bottom: 20px;
}


.input {
  height: 100%;
  flex: 1 1 auto;
  font-size: 18px;
  line-height: 150%;
  padding: 10px;
  resize: none;
  overflow: auto;
}

.button {
  flex: 0 1 100px;
  padding: 10px 20px;
}